import s from "./ProfileInfo.module.css";
import React from "react";

const AdditionalInformation = ({profile, isOwner, goToEditMode}) => {

    return (
        <div>
            <div>
                <b>My full name: </b>{profile.fullName}
            </div>
            <div>
                <b>About me: </b>{profile.aboutMe}
            </div>
            <div>
                <b>Looking for a job: </b>{profile.lookingForAJob ? "Yes" : "No"}
            </div>
            {profile.lookingForAJob &&
                <div>
                    <b>My professional skills: </b>{profile.lookingForAJobDescription}
                </div>
            }
            <div><b>My contacts: </b>{Object.keys(profile.contacts)
                .map(key => <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]}/>)}</div>
            {isOwner && <div><button className={s.button} onClick={goToEditMode}>Edit</button></div>}
        </div>)
}


export const Contact = ({contactTitle, contactValue}) => {
    return <div className={s.contacts}>
        <b>{contactTitle}: </b>{contactValue}
    </div>
}


export default AdditionalInformation;