import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/preloader";
import ProfileStatusWithHooks from "../ProfileStatusWithHooks";
import avatarPhoto from './../../../assets/images/avatarPhoto.jpg'
import React from "react";
import ShowMore from "../../common/Showmore";
import {useState} from "react";
import ProfileDataForm from "./ProfileDataForm";
import ProfileDataReduxForm from "./ProfileDataForm";


const ProfileInfo = ({profile, savePhoto, saveProfile, isOwner, status, updateStatus}) => {

    let [editMode, setEditMode] = useState(false)

    if (!profile) {
        return <Preloader/>
    }

    const onMainAvatarSelector = (e) => {
        if (e.target.files.length) {
            savePhoto(e.target.files[0])
        }
    }

    const onSubmit = (formData) => {
        saveProfile(formData).then (
            () => {setEditMode(false)}
        )
    }

    return (
        <div>
            {/* <div className={s.fon}>
                <img
                    src="https://images.ctfassets.net/hrltx12pl8hq/7yQR5uJhwEkRfjwMFJ7bUK/dc52a0913e8ff8b5c276177890eb0129/offset_comp_772626-opt.jpg?fit=fill&w=800&h=300"
                    alt="first-photo"/>
            </div>*/}
            <div className={s.descriptionBlock}>
                <img src={profile.photos.large || avatarPhoto} className={s.avatar} alt="photo"/>
                {isOwner && <input type={"file"} onChange={onMainAvatarSelector}/>}
                <div className={s.info}>
                    <ProfileStatusWithHooks className={s.status} status={status}
                                            updateStatus={updateStatus}/>
                    {editMode
                        ? <ProfileDataReduxForm initialValues={profile} profile={profile} onSubmit={onSubmit}/>
                        : <ShowMore profile={profile} isOwner={isOwner}
                                    goToEditMode={()=>{setEditMode(true)}}/>}
                </div>
            </div>
        </div>
    );
}


export default ProfileInfo;
