import React from "react";
import {Field, reduxForm} from "redux-form";
import {Input, Textarea} from "../../common/FormsControls/FormsControls";
import {Contact} from "./Additionalnformation";
import s from "./ProfileInfo.module.css";
import styles from "../../common/FormsControls/FormsControls.module.css";

const ProfileDataForm = ({handleSubmit, profile, error}) => {
    return (
        <form onSubmit={handleSubmit}>
            <div>
                <b>My full name: </b>
                <Field component={Input} name={'fullName'} type="text" placeholder={'My full name'}
                       validate={[]}/>
            </div>
            <div>
                <b>About me: </b>
                <Field component={Textarea} name={'aboutMe'} type="text" placeholder={'About me'}
                       validate={[]}/>
            </div>
            <div>
                <b>Looking for a job: </b>
                <Field component={Input} name={'lookingForAJob'} type="checkbox"
                       validate={[]}/>
            </div>
            <div>
                <b>My professional skills: </b>
                <Field component={Textarea} name={'lookingForAJobDescription'} type="text"
                       placeholder={'My professional skills'} validate={[]}/>
            </div>
            <div>
                <b>My contacts: </b>{Object.keys(profile.contacts).map(key => {
                return (
                    <div className={s.contacts} key={key}>
                        <b>{key}</b> <Field component={Input} name={'contacts.' + key} type="text"
                                            placeholder={key} validate={[]}/>
                    </div>
                )
            })}
            </div>
                { error && <div className={styles.formSummaryError}>{error}</div>}
            <div>
                <button className={s.button}>Save</button>
            </div>
        </form>
    )
}

const ProfileDataReduxForm = reduxForm({form: 'profileData'})(ProfileDataForm)

export default ProfileDataReduxForm
