import React, {useState} from "react";
import AdditionalInformation from "../Profile/ProfileInfo/Additionalnformation";
import s from '../Profile/ProfileInfo/ProfileInfo.module.css'


const ShowMore = ({profile, isOwner, goToEditMode}) => {
    let [show, toggleShow] = useState(true);
    return (
    <div>
        {show && <AdditionalInformation profile={profile} isOwner={isOwner}
                                        goToEditMode={goToEditMode}/>}
        <button className={s.button} onClick={() => toggleShow(!show)}>
            {show ? "Hide" : "Show additional information"}
        </button>
    </div>)
}

export default ShowMore;